import React from 'react'

//import custom material-ui styles
import { Grid } from '@material-ui/core'
import LoginButton from '../mui-modifiedComponents/Login-button'
import {makeStyles} from '@material-ui/core/styles'
import dispotifyLogo from '../Img/Dispotify.png';

const useStyles = makeStyles({
    root:{
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundImage: 'linear-gradient(#3f51b5, #101533)'        
    },
    box:{
        minHeight: '50%',
        borderRadius: '20px 20px 20px 20px',
        padding: '100px',
        background: '#F4F4F4',
        boxShadow: '10px 10px 66px 3px rgba(0,0,0,0.75)',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
        alignItems: 'center',
    },
    image:{
        height: '250px',
        width: '250px'
    }
})


function Start(){
    const classes = useStyles();
    return(
        <Grid item xs={12} sm={12} md={12} className= {classes.root}>
                <Grid item xs={10} sm={6} md={4} className ={classes.box}>
                    <img src={dispotifyLogo} alt="Logo Dispotify" className={classes.image}/>
                    <LoginButton/>
                </Grid>
        </Grid>
    ); 
}

export default Start;