import React from 'react';
import {ListItemText,ListItemIcon,ListItem,List,Divider} from '@material-ui/core'
import {PlaylistPlay, Android, YouTube, Favorite, PlayCircleFilled} from '@material-ui/icons'

//imports material-ui
import {Grid} from '@material-ui/core/'

function DrawerMenu(){
    return  <Grid id="gridMenu">
                {/*<div className={classes.toolbar} /-->*/}
                <Divider />
                <List>
                    <ListItem button>
                      <ListItemIcon>
                        <PlaylistPlay />
                      </ListItemIcon>
                      <ListItemText primary="Mi Spotify" />
                    </ListItem>
                    <ListItem button>
                      <ListItemIcon>
                        <Android />
                      </ListItemIcon>
                      <ListItemText primary="Mi bot" />
                    </ListItem>
                    <ListItem button>
                      <ListItemIcon>
                        <YouTube />
                      </ListItemIcon>
                      <ListItemText primary="Mi youtube" />
                    </ListItem>
                </List>
                <Divider />
                <List>
                    <ListItem button>
                      <ListItemIcon>
                        <Favorite />
                      </ListItemIcon>
                      <ListItemText primary="Estado del bot" />
                    </ListItem>
                    <ListItem button>
                      <ListItemIcon>
                        <PlayCircleFilled />
                      </ListItemIcon>
                      <ListItemText primary="Reproductor" />
                    </ListItem>
                </List>
              </Grid>
}

export default DrawerMenu