import React, { Component } from 'react';

//importación de las vistas de la app
import Start from './views/Start.jsx';
import Home from './views/Home.jsx';

//imports spotify
import hash from "./hash";

//import custom material-ui styles
import NavBar from './mui-modifiedComponents/NavBar-app'

class App extends Component {

  constructor(){
    super();
    this.state ={
      seccion1: "sectionStart",
      seccion2: "sectionHome",
      menuVisibility: true
    }
  }

  componentDidMount(){
    let _token = hash.access_token;
    if(localStorage.getItem("token")){
      _token=localStorage.getItem("token");
    }
    if (_token) {
      // Set token
      localStorage.setItem("token",_token);
      this.setState({
        token: _token
      });
      this.setCurrentlyUser(_token);
      this.setList(_token);
      this.moveHome();
      console.log("version React:",React.version);
    }
  }

  setCurrentlyUser(token) {
    // Obtiene el nombre del usuario y llama a la funcion que trae sus listas
    fetch("https://api.spotify.com/v1/me",{
      headers:{
        Accept: "application/json",
        Authorization: "Bearer "+token,
        "Content-Type": "application/json"
      },
      params:{
        market: "ES",
        offset:"5"
      }
    })
    .then(res =>{
      return res.json();
    })
    .then(data =>{
      this.setState({
        display_name: data.display_name,
      });
      this.getLists(data.id,token);
    })
    .catch(error=> error)
  }

  setList(token) {
    // Trae la lista de guardados del usuario
    fetch("https://api.spotify.com/v1/me/tracks?limit=50&offset=0",{
      headers:{
        Accept: "application/json",
        Authorization: "Bearer "+token,
        "Content-Type": "application/json"
      },
      params:{
        market: "ES",
        offset:"5"
      }
    })
    .then(res =>{
      return res.json();
    })
    .then(data =>{
        var spotifyList = [];
        data.items.forEach(element => {
          spotifyList.push({
            nombreCancion: element.track.name,
            album: element.track.album.name,
            nombreArtista: element.track.artists[0].name,
            imagen: element.track.album.images[0].url,
            id: element.track.id

          })
        });
        this.setState({
          songList: spotifyList
        });
    })
    .catch(error=> error)
  }

  getLists(id,token){
    fetch("https://api.spotify.com/v1/users/"+id+"/playlists",{
      headers:{
        Accept: "application/json",
        Authorization: "Bearer "+token,
        "Content-Type": "application/json"
      },
      params:{
        market: "ES",
        offset:"5"
      }
    })
    .then(res =>{
      return res.json();
    })
    .then(data =>{
        console.log(data);

    })
    .catch(error=> error)
  }

  getFavoriteList(idList,token){
    fetch("https://api.spotify.com/v1/playlists/"+idList+"/tracks",{
      headers:{
        Accept: "application/json",
        Authorization: "Bearer "+token,
        "Content-Type": "application/json"
      },
      params:{
        market: "ES",
        offset:"5"
      },

    })
    .then(res =>{
      return res.json();
    })
    .then(data =>{
        var favSpotifyList = [];
        data.items.forEach(element => {
          favSpotifyList.push({
            nombreCancion: element.track.name,
            nombreArtista: element.track.artists[0].name,
            album: element.track.album.name,
            imagen: element.track.album.images[0].url,
            id: element.track.id

          })
        });
        this.setState({
          favSong_List: favSpotifyList
        })
    })
    .catch(error=> error)

  }

  endSesion(){
    localStorage.clear();
    window.location.reload(false);
  }
  
  moveStart(){
    let section = document.getElementsByTagName("section"); 
    let index  = Object.keys(section);
    index.forEach(element => {
        section.item(element).style.display = "none";
    });
    document.getElementById(this.state.seccion1).style.display ="initial";
  }

  moveHome(){
    let section = document.getElementsByTagName("section"); 
    let index  = Object.keys(section);
    index.forEach(element => {
        section.item(element).style.display = "none";
    });
    document.getElementById(this.state.seccion2).style.display ="initial";
  }

  changeMenuVisibility(){
    if(this.state.menuVisibility === true){
      this.setState({menuVisibility:false})
    }else{
      this.setState({menuVisibility:true})
    }

  }


  render() {
    return (
        <div className="App" >
          <div style={{position:'fixed', zIndex: 2, width: '100%'}}>
            <NavBar
              token=  {this.state.token}
              displayName= {this.state.display_name} 
              funMoveHome= {this.moveHome.bind(this)}
              endSesion= {this.endSesion.bind(this)}
              changeMenuVisibility={this.changeMenuVisibility.bind(this)}
            />
          </div>
          <section id="sectionStart">
              <Start 
                token = {this.state.token} 
                funMoveHome={this.moveHome.bind(this)} 
              />
            </section>
          <section id="sectionHome" style={{display: "none"}}>
              <Home 
                menuVisibility= {this.state.menuVisibility}
                listFav={this.state.favSong_List} 
                favList={this.state.favList} 
                song_List ={this.state.songList} 
                token = {this.state.token} 
                progress_ms={this.state.progress_ms}  
                is_playing={this.state.is_playing} 
              />
            </section>
        
        </div>
    );
  }
}

export default App;
