import React from "react";
import ResponsiveDrawer from "../mui-modifiedComponents/Drawer-menu"

import {makeStyles,Grid} from '@material-ui/core/'
import MediaControlCard from '../mui-modifiedComponents/Media-control-card'

const useStyles = makeStyles({
  root: {
    display: 'flex',
    justifyContent: 'center'
  },
  app:{
    maxWidth: '80%',
    marginTop: '60px',
    marginBottom: '10px',
    marginLeft: '20px'
  },
  MiSpotify:{
    display: 'flex',
    justifyContent: 'space-around',
    flexWrap: 'wrap'

  },
  MiBot:{
    background:'red',
    display: 'none'
  },
  Menu:{
    background: '#f1f1f1',
    zIndex: 1,
    marginTop: '60px',
  }
});

function Home(props){
  const classes = useStyles();
  return  <Grid className={classes.root}>
            {props.menuVisibility && <Grid item xs={6} sm={3} md={3} className={classes.Menu} >
                <ResponsiveDrawer />
            </Grid>}
            <Grid className={classes.app}>
                <Grid className={classes.MiSpotify}>
                  {props.song_List && props.song_List.map((card,index)=>(
                    <MediaControlCard 
                      key={index}
                      album= {card.album}
                      imagen= {card.imagen}
                      artista= {card.nombreArtista}
                      cancion= {card.nombreCancion}
                    />
                  ))}
                </Grid>
                  
                <Grid className={classes.MiBot}>
                  bot
                </Grid>
            </Grid>
          </Grid>

}

export default Home;
