import React from 'react'
import {AppBar,Toolbar,Button, IconButton} from "@material-ui/core"
import {Menu, MusicNoteTwoTone} from '@material-ui/icons';
import {makeStyles} from '@material-ui/core/styles'

const useStyles = makeStyles({
    root: {
        background: 'black'
    },
    title:{
        marginLeft : '10px',
        flexGrow: 1
    }
})

function NavBar(props){
    const classes = useStyles();

    return(
        <div>
            <AppBar className={classes.root} position="static">
                <Toolbar> 
                    <IconButton color="inherit" onClick={()=>{props.changeMenuVisibility()}}>
                        <Menu />
                    </IconButton>
                    <Button variant="contained" color="primary"  onClick={props.token && props.funMoveHome.bind(this) }>
                        <MusicNoteTwoTone/>
                        Dispotify
                    </Button>
                    <h4 className={classes.title} color="secondary">{props.displayName}</h4>
                    {props.token && (
                        <Button variant="contained" color="primary" onClick={props.endSesion.bind(this)}>
                            salir
                        </Button>
                    )}
                </Toolbar>
            </AppBar>
        </div>
    );
}


export default NavBar